  margin-top: 1rem;
  padding-top: 1rem;
  display: flex;
  justify-content: space-between; }
  .page-wrap li a:hover {
    color: #AA0601; }
  .page-wrap li .icon {
    position: relative; }
    .page-wrap li .icon img {
      width: 12px; }
  .page-wrap li:first-child .icon img {
