  @media (max-width: 920px) {
    .page-wrap {
      flex-direction: column; }
      .page-wrap li {
        margin-bottom: 5px; }
        .page-wrap li:last-child a {
          display: flex;
          align-items: center; }
          .page-wrap li:last-child a .icon {
            order: -1;
            margin-right: 5px; } }

.pageWrap {
  display: flex;
  justify-content: center;
  margin: 2rem 0 3rem; }
  .pageWrap li {
    display: inline-block;
    border: #555555 1px solid;
    margin-left: -1px; }
    .pageWrap li:last-child {
      background-color: #F2F2F2; }
    .pageWrap li a {
      font-family: "Roboto";
      color: #555555;
      display: block;
      padding: 10px 20px;
      text-align: center; }
    .pageWrap li:hover, .pageWrap li.active {
      background-color: black; }
      .pageWrap li:hover a, .pageWrap li.active a {
