  padding: .5rem 1rem;
  margin-bottom: 1rem;
  list-style: none;
  border-radius: .25rem; }
  .breadcrumb li + li ::before {
    display: inline-block;
    padding-right: .5rem;
    padding-left: .5rem;
    color: #6c757d;
    content: "/"; }
  .breadcrumb li a {
    font-size: 14px; }
    .breadcrumb li a:hover, .breadcrumb li a.active {
      color: #AA0601; }
  @media (max-width: 920px) {
    .breadcrumb {
      padding-left: 0;
      margin-bottom: 0; } }
